@import url(https://fonts.googleapis.com/css2?family=Cantarell&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merienda&display=swap);
/* #5ce1e6 --> primary Color */
* {
  margin: 0px;
  padding: 0px;
  font-family: "Merienda", sans-serif !important;
  color: black;
}

.navBar_bg {
  /* background-color: #00fff30a !important; */
  box-shadow: 0px 0px 8px 0px #4cbbbf;
}
.navBar_text > ul > li > a {
  font-family: "Merienda", sans-serif !important;
  text-transform: uppercase;
  font-weight: 550;
}
.navBar_logo {
  width: 125px;
}
.navBar_activeLink {
  font-family: "Merienda", sans-serif !important;
  text-transform: uppercase;
  font-weight: 500;
  color: #0bf1dc !important;
  transition-property: all !important;
  transition-duration: 0.6s !important;
  transition-timing-function: cubic-bezier(0.39, 0.58, 0.57, 1) !important;
  border-bottom: 2px solid black !important;
}
.header_BannerImage {
  width: 400px;
}
.header_Container {
  /* background-color: rgb(211, 210, 210); */
}
.header_TextHeading {
  font-size: 30px;
  line-height: 2.5rem;
}
.header_TextHeading_Always {
  font-size: 40px;
  color: #5ce1e6;
}
.header_TextHeading_Learning {
  font-size: 40px;
  color: black;
}

.header_Banner_TagLine {
  font-size: 1.1rem;
  color: grey;
  line-height: 1.5rem;
}
.header_getStarted_btn {
  border-radius: 50px !important;
  padding: 5px 20px !important;
}

/* Footer */
.footer_bg {
  background-color: #00fff31f !important;
  color: grey;
}
.footer_bg_band {
  background-color: #00fff347 !important;
  color: #2b4141;
}
.footer_heartIcon {
  margin: 0px 5px;
  color: darkred;
}
.footer_logo {
  max-width: 200px !important;
}
.footer_lightText {
  font-size: 0.8rem;
  color: grey;
}
.footer_darkText {
  color: rgb(43, 65, 65);
}
.footer_lightText > a {
  text-decoration: none;
  color: grey;
}
.footer_lightText > a:hover {
  text-decoration: none;
  color: #0bf1dc;
}
.footer_textUnderline {
  border-bottom: 2px solid #0bf1dc;
  padding-bottom: 5px;
}

@media only screen and (max-width: 450px) {
  .header_TextHeading {
    font-size: 20px;
    line-height: 2rem;
  }
  .header_TextHeading_Always {
    font-size: 30px;
    color: #5ce1e6;
  }
  .header_TextHeading_Learning {
    font-size: 30px;
    color: black;
  }

  .header_Banner_TagLine {
    font-size: 0.8rem;
    color: grey;
    line-height: 1rem !important;
  }
  .header_getStarted_btn {
    border-radius: 50px !important;
    padding: 5px 20px !important;
  }
}

.card_size {
  width: 150px;
  border: none;
  margin: 0.5rem;
  /* box-shadow: 0.5px 0.5px 3px 1px lavender; */
  transition: all 0.3s linear;
  cursor: pointer;
}

.card_image {
  width: 100%;
  object-fit: cover;
}
.card_size:hover {
  border: grey;
  box-shadow: 2px 2px 10px 2px rgb(211, 209, 209);
  transform: scale(1.01) translate3d(0px, -2px, 8px);
}
@media only screen and (max-width: 450px) {
  .card_size {
    width: 90px;
  }
}

.row_TextHeading {
  font-size: 30px;
  color: #5ce1e6;
}
.row_description {
  font-size: 1.1rem;
  color: grey;
  line-height: 1.5rem;
}
.row_checkAll_btn {
  border-radius: 50px !important;
  padding: 5px 20px !important;
}
@media only screen and (max-width: 450px) {
  .row_TextHeading {
    font-size: 25px;
    color: #5ce1e6;
  }
  .row_description {
    font-size: 0.8rem;
    color: grey;
    line-height: 1rem;
  }
  .row_checkAll_btn {
    font-size: 12px;
    border-radius: 50px !important;
    padding: 5px 20px !important;
  }
}

.Testimonial_card {
  font-size: 12px;
  border-radius: 10px;
  box-sizing: border-box !important;
  /* background-color: #aee1f9; */
  background-image: linear-gradient(315deg, #b8efff 0%, #e0fffe4a 74%);
}

.form_container {
  display: flex;
  flex-direction: column;
  padding: 0.4rem;
}
.row_custom_style {
  font-size: 14px;
}
.input_div {
  display: flex;
  display: flex;
  flex-direction: column;
  padding: 0.4rem;
  padding-bottom: 2rem;
  position: relative;
  width: 90%;
}
.input_div label {
  position: absolute;
  top: 0px;
  left: 0px;
  transition: all 200ms linear;
}
.input_div input:focus + label {
  color: #0bf1dc;
  top: -20px;
  left: 0px;
}
.input_div input:valid + label {
  color: #0bf1deab;
  top: -20px;
  left: 0px;
}

.form_container input {
  border: none;
  outline: none;
  border-bottom: 1px solid black;
  margin-bottom: 5px;
  padding: 1px 5px;
}
.form_container button {
  width: 150px;
  margin-top: 1.5rem;
  border: none;
  font-size: 1.3rem;
  font-weight: bold;
  padding: 5px 10px;
  border: 2px solid rgba(35, 213, 154, 0.796);
  background-color: transparent;
  border-radius: 3px;
  transition: all 0.2s linear;
}
.form_container button:hover {
  border: 2px solid aquamarine;
  background-color: #0bf1dc;
  color: white;
}
.social_icon > i {
  font-size: 40px;
  font-family: sans-serif !important;
}
@media only screen and (max-width: 450px) {
  .address_column {
    margin-top: 75px;
  }
}

.footer_banner_box {
  height: 250px;

  object-fit: cover;
  background-position: center;
  background-image: url(/static/media/footerBanner.3c482d2d.jpg);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer_banner_box::before {
  content: "";
  background-color: rgb(127 243 255 / 13%);
  position: absolute;
  top: 0px;
  left: 0px;
  width: 98vw;
}
.about_text p {
  line-height: 2rem;
}

